.slider {
	&.slider-neutral {
		& .noUi-connect,
        &.noUi-connect {
            background-color: $white;
        }

		&.noUi-target {
			background-color: rgba(255,255,255, .3);
		}

		& .noUi-handle {
			background-color: $white;
		}
	}

	&.slider-primary {
		& .noUi-connect,
        &.noUi-connect {
            background-color: $primary;
        }

		&.noUi-target {
			background-color: $primary-opacity;
		}

		& .noUi-handle {
			background-color: $brand-primary;
			box-shadow: 0px 0px 10px 0px $brand-primary;
		}
	}

    &.slider-info {
        & .noUi-connect,
        &.noUi-connect {
            background-color: $brand-info;
        }

		&.noUi-target {
			background-color: $info-opacity;
		}

		& .noUi-handle {
			background-color: $brand-info;
			box-shadow: 0px 0px 10px 0px $brand-info;
		}
    }
    &.slider-success {
        & .noUi-connect,
        &.noUi-connect{
            background-color: $brand-success;
        }

        &.noUi-target {
			background-color: $success-opacity;
		}

		& .noUi-handle {
			background-color: $brand-success;
			box-shadow: 0px 0px 10px 0px $brand-success;
		}
    }
    &.slider-warning {
        & .noUi-connect,
        &.noUi-connect {
            background-color: $brand-warning;
        }

        &.noUi-target {
			background-color: $warning-opacity;
		}

		& .noUi-handle {
			background-color: $brand-warning;
			box-shadow: 0px 0px 10px 0px $brand-warning;
		}
    }
    &.slider-danger {
        & .noUi-connect,
        &.noUi-connect {
            background-color: $brand-danger;
        }

        &.noUi-target {
			background-color: $danger-opacity;
		}

		& .noUi-handle {
			background-color: $brand-danger;
			box-shadow: 0px 0px 10px 0px $brand-danger;
		}
    }

}
