.td-empleado {
    display: flex;
    align-items: center;
    &-image {
      margin-right: 10px;
      img{
        object-fit:cover;
      }
    }
    &-nombre {
      display: flex;
      flex-direction: column;
    }
  }
  
  .td-edit{
      svg{
          font-size: 20px;
          cursor: pointer;
          transition: 300ms ease-in-out;
          color: #667085;
          &:hover{
              opacity: 0.8;
          }
      }
  }
  
  .table-container {
    margin-top: 40px;
    overflow: auto;
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
      border-radius: 10px;
      background-color: transparent;
    }

    &::-webkit-scrollbar {
      width: 8px;
      height: 8px;
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
      background-color: #555;
    }


    @media (max-width: 976px) {
      overflow: auto;
    }

    .table-header-container{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }
    .table-company {
      overflow: auto;
      width: 100%;
      // border-radius: 8px;
      border: none;
      color: #96a0aa;
      margin-top: 20px;
      margin-bottom: 10px;
      border-collapse: separate !important;
      border-bottom: 1px solid #FFF;
      padding-bottom: 1.5em;
      // box-shadow: 0px 2px 4px -2px gray;
      thead {
        background-color: transparent;
        font-weight: bold;
        color: rgba(255, 255, 255, 0.7);
        
        th {
          border-bottom: 1px solid gray;
        }
      }
  
      tbody {
        max-width: 100%;
        tr {
          border: 10px solid #dfe6ee;
  
          td {
            padding: 12px;
  
            svg{
              cursor: pointer;
              transition: 300ms ease-in-out;
              &:hover{
                  opacity: 0.8;
              }
          }
          }
        }

        tr:hover{
          background-color: rgba(0, 0, 0, 0.03);
        }
      }
  
      .table-btn {
        border-radius: 16px;
        font-size: 12px;
        border: none;
        font-weight: 600;
        transition: 300ms ease-in-out;
        &:hover {
          opacity: 0.8;
        }
      }
  
      .active {
        color: #027a48;
        background: #ecfdf3;
      }
      .inactive{
          color: rgb(161, 0, 0);
          background-color: rgb(247, 60, 60);
      }
    }
  }
  
  .pagination-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    // padding-top: 30px;
    // border-top: 1px solid #dfe6ee;
    .pagination-button {
      background: none;
      border: none;
      color: #96a0aa;
      padding: 16px;
      transition: 300ms ease-in-out;
      border-radius: 50%;
      &:hover {
        background: rgba(249, 245, 255, 0.356);
        color: #3252a0;
        font-weight: 600;
      }
    }
  
    .active {
      background: rgba(249, 245, 255, 0.356);
      color: #3252a0;
      font-weight: 600;
    }
  
    .side-pagination-button {
      display: flex;
      align-items: center;
      button {
        &:disabled:hover {
          background-color: transparent;
          color: #96a0aa;
          font-weight: normal;
        }
      }
    }
  }
  
  .table-input{
      label{
          margin-bottom: 5px;
          font-size: 14px;
          color: #344054;
          font-weight: 500;
      }
      input, select{
          margin-right: 20px;
          padding: 5px 10px;
          border-radius: 8px;
          outline: none;
          border: 1px solid #D0D5DD;
  
          &::placeholder{
              font-weight: 500;
          }
      }
  }

  .table__button {
    color: white;
    cursor: pointer;
  }

  .table__button:hover {
    text-decoration: underline;
  }

  .preview__subcategory {
    border-radius: 8px;
    padding: .5rem 1rem;
    border: 1px solid rgb(33, 31, 61);
    display: inline-block;
    // min-width: 25%;
    // max-width: 50%;

    &:hover {
      background-color: #96a0aa;
    }
  }

  .sede__menu-item {
    cursor: pointer;
    padding: 1rem 0;
    list-style: none;
  }

  .sede__menu-item:hover {
    border-bottom: 1px solid rgba(240, 240, 240, 0.5);
    background-color: rgba(0, 0, 0, 0.06);
  }
  
  .sede__submenu-item {
    padding: 1rem 0;
    list-style: none;
    cursor: pointer;
  }
  .sede__submenu-item:hover {
    background-color: rgba(0, 0, 0, 0.06);
    border-bottom: 1px solid rgba(240, 240, 240, 0.5);
  }

  .hidde__sublist {
    display: none;
  }

  .show__sublist {
    display: block;
  }